  created() {
    this.getTime();
  },
  methods: {
    getTime() {
      var establish = new Date(this.setUpTime.replace(/-/g, "/"));
      var current = new Date();
      var time = current.getTime() - establish.getTime();
      var days = parseInt(time / (1000 * 60 * 60 * 24));
      this.establishTime = days;
    }
  }
}
